import React from 'react'
import {Redirect, Route, Switch} from 'react-router-dom'
import {NavbarMenu} from "./components/NavbarMenu";
import {MainPage} from "./components/Main/MainPage";
import {ContactsPage} from "./components/ContactsPage/ContactsPage";
import FooterComp from "./components/Footer-Component";
import PageContainer from "./components/PageContainer";
import LinkListCategories from "./data/LinksToPages/linkListCategories";
import linkListSubcategoriesBra from "./data/LinksToPages/linkListSubcategoriesBra";
import linkListSubcategoriesKnickers from "./data/LinksToPages/linkListSubcategoriesKnickers";
import SportsBra from "./components/ProductPages/SportsBra";
import Set from "./components/ProductPages/Set";
import Accessories from "./components/ProductPages/Accessories";

export const useRoutes = () => {
    return (
        <div>
            <NavbarMenu/>
            <Switch>
                <Route path="/" exact>
                    <PageContainer Page={<MainPage/>}/>
                </Route>
                <Route path="/contacts" exact>
                    <PageContainer Page={<ContactsPage/>}/>
                </Route>
                {LinkListCategories.map(p => {
                        return (
                            <Route key={p.route} path={p.route} exact>
                                <PageContainer Page={p.component}/>
                            </Route>
                        )
                    }
                )}
                {linkListSubcategoriesBra.map(p => {
                        return (
                            <Route key={p.route} path={p.route} exact>
                                <PageContainer Page={p.component}/>
                            </Route>
                        )
                    }
                )}
                {linkListSubcategoriesKnickers.map(p => {
                        return (
                            <Route key={p.route} path={p.route} exact>
                                <PageContainer Page={p.component}/>
                            </Route>
                        )
                    }
                )}
                <Route path="/sport" exact>
                    <PageContainer Page={<SportsBra/>}/>
                </Route>
                <Route path="/set" exact>
                    <PageContainer Page={<Set/>}/>
                </Route>
                <Route path="/accessories" exact>
                    <PageContainer Page={<Accessories/>}/>
                </Route>
                <Redirect to="/"/>
            </Switch>
            <FooterComp/>
        </div>
    )
}
